import React from "react"
import ReactDOM from "react-dom/client"
import "./index.css"
import App from "./App"

const root = ReactDOM.createRoot(document.getElementById("root"))
root.render(<App />)

// Register the service worker
if ("serviceWorker" in navigator) {
  window.addEventListener("load", () => {
    navigator.serviceWorker
      .register("/iSeeU-InstagramTracker/service-worker.js")
      .then((registration) => {
        console.log("Service Worker registered with scope:", registration.scope)

        registration.onupdatefound = () => {
          const installingWorker = registration.installing
          installingWorker.onstatechange = () => {
            // alert(`State changed: ${installingWorker.state}`)
            if (
              installingWorker.state === "installed" ||
              installingWorker.state === "redundant "
            ) {
              if (navigator.serviceWorker.controller) {
                // New content is available; prompt the user to refresh
                if (
                  window.confirm(
                    "New content is available. Would you like to refresh?"
                  )
                ) {
                  window.location.reload()
                }
              }
            }
          }
        }
      })
      .catch((error) => {
        console.error("Service Worker registration failed:", error)
      })
  })
}
